import { getArticle, addPost, updatePost } from '@/api/article'

export default {
  // 在路由匹配组件渲染之前会先执行中间件处理
  middleware: 'authenticated',
  name: 'EditorIndex',
  data() {
    return {
      tag: '',
      post: {
        title: '',
        description: '',
        body: '',
        tagList: []
      }
    }
  },
  async created () {
    if(this.$route.params.slug){
      const {data} = await getArticle(this.$route.params.slug)
      this.post = data.article
    }
  },
  methods: {
    addTag(e) {
      console.log(e)
      if (e.key == 'Enter') {
        this.post.tagList.push(this.tag)
        this.tag = ''
      }
    },
    deleteTag(index) {
      this.post.tagList.splice(index, 1)
    },
    // 发布更新文章
    async onAddPost() {
      let { slug } = this.$route.params
      const {data} = slug
          ? await updatePost(slug, this.post)
          : await addPost(this.post)

      console.log(data)
      // success
      if (data.article) {
        this.$router.push({
          name: 'article',
          params: {
            slug: data.article.slug
          }
        })
      }
    }
  }
}
